html {
  background-color: #b0e7cc;
}

body {
  margin: 0 auto;
  padding: 0;
  font-family: sans-serif;
  color: #3e3e3e;
  text-align: center;
}

img {
  width: 100%;
}

.app {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Hiragino Sans",
    "Hiragino Kaku Gothic ProN", Miryo, sans-serif;
  text-align: center;
}

.app__title {
  font-weight: 600;
}

.heartIcon {
  color: #ff6e6e;
  font-size: 40px;
}

.cards {
  position: relative;
  margin: 20px auto;
  position: relative;
  height: 300px;
  width: 300px;
  overflow: hidden;
  background-color: #fff;
  border-radius: 10px;
}

.card {
  background-color: white;
  background-size: cover;
  position: absolute;
  background: #f8f3f3;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  transition: box-shadow 0.3s;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
  cursor: pointer;
}

.card__info {
  position: absolute;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 15px 10px;
  background-color: rgba(255, 255, 255, 0.8);
  box-sizing: border-box;
}

.card__name {
  font-size: 20px;
  font-weight: 600;
}

.animate {
  transition: transform 0.3s;
  box-shadow: none;
}

.inactive {
  box-shadow: none;
}

.alert {
  width: 45%;
  min-height: 10%;
  position: absolute;
  z-index: 9999;
  opacity: 0;
  transition: opacity 0.5s;
  color: white;
  vertical-align: middle;
  line-height: 3rem;
}

.alert-visible {
  opacity: 1;
}

.alert-right {
  top: 0;
  right: 0;
  background: #ff6e6e;
  border-top-left-radius: 50px;
  border-bottom-left-radius: 50px;
  font-weight: bold;
}

.alert-left {
  top: 0;
  left: 0;
  background: #ff6e6e;
  border-top-right-radius: 50px;
  border-bottom-right-radius: 50px;
  font-weight: bold;
}
